import React from "react";
import PropTypes from "prop-types";
import Popup from "./PopupDom";
import Detail from "./Detail";
import './Movie.css';

class Movie extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.id,
            title: props.title,
            slug: props.slug,
            genres: props.genres,
            rating: props.rating,
            large_cover_image: props.large_cover_image,
            year: props.year,
            summary: props.summary,
            yt_trailer_code: props.yt_trailer_code,
            state: props.state
        };

        this.openMovie = this.openMovie.bind(this);
        this.closeMovie = this.closeMovie.bind(this);
        this.handleError = this.handleError.bind(this);
    }

    openMovie() {
        this.setState({ isOpenMovie: true });
    }

    closeMovie() {
        this.setState({ isOpenMovie: false });
    }

    handleError = () => {
        this.props.onError(this.state.id);
    }

    render() {
        const { title, slug, genres, rating, large_cover_image, year, summary, yt_trailer_code, state, isOpenMovie } = this.state;
        return (
            <div className="movie">
                <div className="figure" onClick={this.openMovie}>
                    <img src={large_cover_image} alt={title} title={title} onError={this.handleError} />
                    <div className="figcaption">
                        {rating && 0 < rating ? <div>{rating} / 10</div> : null}
                        {genres && genres.map((genre, index) => <div key={index}>{genre}</div>)}
                    </div>
                </div>
                <div className="title" onClick={this.openMovie}>
                    <span>{title}</span>
                </div>
                <div className="year">
                    <span>{year}</span>
                </div>
                {isOpenMovie &&
                    <Popup>
                        <Detail
                            title={title}
                            slug={slug}
                            genres={genres}
                            rating={rating}
                            large_cover_image={large_cover_image}
                            year={year}
                            summary={summary}
                            yt_trailer_code={yt_trailer_code}
                            state={state}
                            onClose={this.closeMovie}
                        />
                    </Popup>}
            </div>
        );
    }
}

Movie.propTypes = {
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    slug: PropTypes.string.isRequired,
    genres: PropTypes.arrayOf(PropTypes.string).isRequired,
    rating: PropTypes.number.isRequired,
    large_cover_image: PropTypes.string.isRequired,
    year: PropTypes.number.isRequired,
    summary: PropTypes.string.isRequired,
    yt_trailer_code: PropTypes.string.isRequired,
    state: PropTypes.string.isRequired
}

export default Movie;