import React from 'react';
import PropTypes from "prop-types";
import './Combobox.css';

class Combobox extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: props.name,
            value: props.value.toUpperCase(),
            options: props.options
        };

        this.handleChange = this.handleChange.bind(this);
    }

    handleChange = (e) => {
        this.props.onSelect(e.target.value);
    }

    render() {
        const { name, value, options } = this.state;
        return (
            <form className="cb_box">
                <label className="cb_box__label">{name}</label>
                <select className="cb_box__control" value={value} onChange={this.handleChange}>
                    {options.map((opt, index) => <option key={index} value={opt}>{opt}</option>)}
                </select>
            </form>
        );
    }
}

Combobox.propTypes = {
    name: PropTypes.string.isRequired,
    options: PropTypes.arrayOf(PropTypes.string).isRequired
}

export default Combobox;