import React from 'react';
import Popup from "./PopupDom";
import Detail from "./Detail";
import './SearchItem.css';

class SearchItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.id,
            title: props.title,
            slug: props.slug,
            genres: props.genres,
            rating: props.rating,
            large_cover_image: props.large_cover_image,
            year: props.year,
            summary: props.summary,
            yt_trailer_code: props.yt_trailer_code,
            state: props.state,
        }
        this.openSearch = this.openSearch.bind(this);
        this.closeSearch = this.closeSearch.bind(this);
    }

    openSearch() {
        this.setState({ isSearchPopup: true });
    }

    closeSearch() {
        this.setState({ isSearchPopup: false });
    }

    render() {
        const { title, slug, genres, rating, large_cover_image, year, summary, yt_trailer_code, state, isSearchPopup } = this.state;
        return (
            <div className="search_bar__item">
                <img className="image" src={large_cover_image} alt={title} title={title} onClick={this.openSearch} />
                <div className="search_bar__data" onClick={this.openSearch}>
                    <div className="title">{title}</div><br />
                    <div className="year">{year}</div>
                </div>
                {isSearchPopup &&
                    <Popup>
                        <Detail
                            title={title}
                            slug={slug}
                            genres={genres}
                            rating={rating}
                            large_cover_image={large_cover_image}
                            year={year}
                            summary={summary}
                            yt_trailer_code={yt_trailer_code}
                            state={state}
                            onClose={this.closeSearch}
                        />
                    </Popup>}
            </div>
        );
    }
}

export default SearchItem;