import React from "react";
import axios from "axios";
import Movie from "./Movie";
import Combobox from "./Combobox";
import SearchBar from "./SearchBar";
import "./App.css";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.base_url = "https://yts.mx/api/v2/list_movies.json";
    this.state = {
      isLoading: true,
      sort_by: 'rating',
      order_by: 'desc',
      page: 1
    }
  }

  getMovies = async () => {
    const { sort_by, order_by, page } = this.state;
    const { data: { data: { movies } } } = await axios.get(
      `${this.base_url}?sort_by=${sort_by}&order_by=${order_by}&page=${page}&limit=50`
    );
    this.setState({ movies, isLoading: false });
    
    if (1 < this.state.page) {
      this.setState({
        prevDisable: false
      })
    }
    else {
      this.setState({
        prevDisable: true
      })
    }

    if (this.state.page < 691) {
      this.setState({
        nextDisable: false
      })
    }
    else {
      this.setState({
        nextDisable: true
      })
    }
  }

  componentDidMount() {
    this.getMovies();
  }

  errorImage = (data) => {
    const { movies } = this.state;
    movies.forEach((movie, index) => {
      if (movie.id === data) {
        this.setState({
          movies: [
            ...movies.slice(0, index),
            ...movies.slice(index + 1, movies.length)
          ]
        })
      }
    });
  }

  selectSort = (data) => {
    this.setState({
        isLoading: true,
        sort_by: data.toLowerCase()
    }, () => {
      this.getMovies();
    });
  }

  selectOrder = (data) => {
    this.setState({
      isLoading: true,
      order_by: data.toLowerCase()
    }, () => {
      this.getMovies();
    });
  }

  pagePrev = () => {
    if (1 < this.state.page) {
      this.setState({
        isLoading: true,
        page: this.state.page - 1
      }, () => {
        this.getMovies();
      });
    }
  }

  pageNext = () => {
    if (this.state.page < 691) {
      this.setState({
        isLoading: true,
        page: this.state.page + 1
      }, () => {
        this.getMovies();
      });
    }
  }

  render() {
    const { isLoading, prevDisable, nextDisable, movies, sort_by,  order_by } = this.state;
    return (
      <div className="app">
        <h1 className="title">Movie Box</h1>
        {!isLoading &&
          <div className="ctrl">
            <div className="ctrl__select">
              <Combobox
                key="sort_by"
                name="Sort by"
                value={sort_by}
                options={[
                  "RATING",
                  "TITLE",
                  "YEAR"
                ]}
                onSelect={this.selectSort}
              />
              <Combobox
                key="order_by"
                name="Order by"
                value={order_by}
                options={[
                  "DESC",
                  "ASC"
                ]}
                onSelect={this.selectOrder}
              />
            </div>
            <div className="ctrl__search">
              <SearchBar url={this.base_url} />
            </div>
          </div>}
        <section className="container" id="popup">
          {isLoading ? (
            <div className="loader">
              <span>Loading data...</span>
            </div>
          ) : (
            <div className="movies">
                {movies &&
                  movies.map(movie => (
                    movie && movie.id && movie.title && 0 < movie.year &&
                    <Movie
                      key={movie.id}
                      id={movie.id}
                      title={movie.title}
                      slug={movie.slug}
                      genres={movie.genres}
                      rating={movie.rating}
                      large_cover_image={movie.large_cover_image}
                      year={movie.year}
                      summary={movie.summary}
                      yt_trailer_code={movie.yt_trailer_code}
                      state={movie.state}
                      onError={this.errorImage}
                    />
                  ))}
            </div>
          )}
        </section>
        {!isLoading &&
          <div className="side">
            <button className="side__left" onClick={this.pagePrev} disabled={prevDisable}>
              &lt;
            </button>
            <button className="side__right" onClick={this.pageNext} disabled={nextDisable}>
              &gt;
            </button>
          </div>
        }
      </div>
    );
  }
}

export default App;