import React from 'react';
import './Detail.css';

function Detail(props) {
    return (
        <div className="detail">
            <div className="detail__close" onClick={props.onClose}>X</div>
            <div className="detail__content">
                <h1>{props.title}</h1>
                <img src={props.large_cover_image} alt={props.title} title={props.title} />
                <h6>{props.year}</h6>
                {props.rating && 0 < props.rating ? <h6>{props.rating} / 10</h6> : null}
                {props.genres && props.genres.map((genre, index) => <h6 key={index}>{genre}</h6>)}
                {props.summary &&
                    <div>
                        <h3>Synopsis</h3>
                        <div>{props.summary}</div>
                    </div>
                }
                {props.yt_trailer_code &&
                    <div>
                        <h3>Trailer</h3>
                        <iframe className="detail__video" title={props.slug} src={`https://www.youtube.com/embed/${props.yt_trailer_code}`} allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                    </div>}
            </div>
        </div>
    );
}

export default Detail;